function getValidationProps(fieldsWithError, field) {
  if (fieldsWithError.includes(fieldsWithError.find(el=>el.name===field))) {
    const field_with_error = fieldsWithError.find(el=>el.name===field)
    return {"error":'error',  'helperText': `${field_with_error.error}`};
  }
}

